import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import { useDispatch, useSelector } from "react-redux";
import { postAlarmTime } from "../redux/alarmReducer";

const useStyles = makeStyles((theme) => ({
  container: {
    display: "flex",
    flexWrap: "wrap",
    width: '120px'
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 200,
  },
}));

export default function AlarmTime() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const time = useSelector((state) => new Date(state.alarm.time).toLocaleTimeString( "de-DE", {hour: '2-digit', minute: '2-digit'}));

  const handleChange = (e) => {
    const time = e.target.value.split(":");
    let alarm = new Date().setHours(...time);
    dispatch(postAlarmTime(alarm));
  };
  
  return (
    <form className={classes.container} noValidate>
     <TextField
        id="time"
        label="Alarm clock"
        type="time"
        value={time}
        onChange={handleChange}
        className={classes.textField}
        InputLabelProps={{
          shrink: true,
        }}
        inputProps={{
          step: "300"
        }}
      />
    </form>
  );
}

