import React from 'react';
import { Grid, makeStyles } from '@material-ui/core'
import AlarmOnOff from './alarmOnOff'
import AlarmTime from './alarmTime'

const useStyles = makeStyles( () => ({
  heading: {
    textAlign: 'center',
    padding: '10px',
    fontSize: '1.5rem',
    fontWeight: 'bold',
  }
}));

export default function AlarmController() {
  const classes = useStyles();

  return (
        <Grid container>
          <Grid item xs={12} className={classes.heading}>
            Tageslicht Wecker
          </Grid>
          <Grid item xs={4}>
            <AlarmOnOff />
          </Grid>
          <Grid item xs={8}>
            <AlarmTime />
          </Grid>
        </Grid>
  );
}
