import React from 'react';
import { Grid, makeStyles } from '@material-ui/core';
import PwmSlider from './brightnessSlider';
import TurnOnOff from './LightOnOff';

const useStyles = makeStyles( () => ({
  heading: {
    textAlign: 'center',
    padding: '10px',
    fontSize: '1.5rem',
    fontWeight: 'bold',
  }
}));

export default function LedController() {
  const classes = useStyles();

  return (
        <Grid container>
          <Grid item xs={12} className={classes.heading}>
            LED Steuerung
          </Grid>
          <Grid item xs={4}>
            <TurnOnOff/>
          </Grid>
          <Grid item xs={8}>
            <PwmSlider />
          </Grid>
        </Grid>
  );
}
