const url = `http://${window.location.hostname}/api`;

const requestFactory = path => async (data = '', method = "GET") => {
    if (method === 'GET') {
        return (await window.fetch(new Request(url + path + data, { method }))).json();
    }
    if (method === 'POST' || method === 'PUT') {
        return (await window.fetch(new Request(url + path, {
            method,
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data)
        }))).json();
    }
}

export const ledRequests = requestFactory('/led');
export const alarmRequests = requestFactory('/alarm');