import '@fontsource/roboto';
import React from 'react';
import { makeStyles, Paper } from '@material-ui/core'
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import AlarmController from './components/alarm';
import Clock from './components/clock';
import LedController from './components/ledControll';
import { getAlarm } from './redux/alarmReducer';
import { getLedBrightness } from './redux/ledReducer';

const useStyles = makeStyles(() => ({
  root: {
    // height: "90%",
    maxHeight: "600px",
    width: "90%",
    maxWidth: "600px",
    margin: "auto",
    padding: '15px',
  },
  heading: {
    textAlign: 'center',
    padding: '10px',
    fontSize: '1.5rem',
    fontWeight: 'bold',
  }
}));

function App() {
  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getLedBrightness());
    dispatch(getAlarm());

    const iv = setInterval(() => {
      dispatch(getLedBrightness());
      dispatch(getAlarm());
    }, 2000);

    return () => clearInterval(iv);
  },[]);

  return (
    <Paper elevation={4} className={classes.root}>
      <Clock />
      <LedController />
      <AlarmController />
    </Paper>
  );
}

export default App;
