import { createSlice } from '@reduxjs/toolkit'
import { ledRequests } from '../requests.js';

export const getLedBrightness = () => async (dispatch, getState) => {
  const brightness = (await ledRequests()).data;
  dispatch(dimm(brightness));
}

export const postLedBrightness = (value) => async (dispatch, getState) => {
  const brightness = (await ledRequests({brightness: value}, 'POST')).data;
  console.log(brightness);
  dispatch(dimm(brightness));
}

export const ledSlice = createSlice({
  name: 'led',
  initialState: {
    status: 'off',
    brightness: 0
  },
  reducers: {
    dimm: (state, action) => {
        state.brightness = action.payload;

        if(state.brightness === 0) {
          state.status = 'off'
        } else {
          state.status = 'on'
        }
      }
    },
    turnOn: state => {
      state.brightness = 100;
      state.status = 'on';
    },
    turnOff: state => {
      state.brightness = 0;
      state.status = 'off';
    }
  }
)

export const getBrightness = state => state.led.brightness;

export const { dimm, turnOn, turnOff } = ledSlice.actions

export default ledSlice.reducer