import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import IOSSwitch from "./iosSwitch";
import { useDispatch, useSelector } from "react-redux";
import { postLedBrightness } from "../redux/ledReducer";

const useStyles = makeStyles(() => ({
  root: {
    width: "120px",
    textAlign: "right",
    display: "flex",
    flexDirection: "row",
  },
  col: {
    display: "flex",
    margin: "auto",
  },
}));

export default function TurnOnOff() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const status = useSelector((state) => state.led.status);

  const handleChange = () => {
    if (status === "off") {
        dispatch(postLedBrightness(100));
    } else if (status === "on") {
        dispatch(postLedBrightness(0));
    }
  };

  return (
    <div className={classes.root}>
      <div className={classes.col}>LEDs:</div>
      <div className={classes.col}>
        <IOSSwitch onChange={handleChange} checked={status === "on"} />
      </div>
    </div>
  );
}
